import ActivityTypeSelector from '@/components/Activity/ActivityTypeSelector';
import ActivityStatusSelector from '@/components/Activity/ActivityStatusSelector';
import ActivityLocationCascader from '@/components/Activity/ActivityLocationCascader';
import CommonCardFrame from '@/components/CommonCardFrame';
var formItemLayout = {
  labelCol: {
    span: 4
  },
  wrapperCol: {
    span: 4
  }
};
export default {
  components: {
    CommonCardFrame: CommonCardFrame,
    ActivityLocationCascader: ActivityLocationCascader,
    ActivityStatusSelector: ActivityStatusSelector,
    ActivityTypeSelector: ActivityTypeSelector
  },
  props: {
    dataSource: {
      required: true,
      type: Object,
      default: function _default() {
        return {
          type: {},
          location: []
        };
      }
    }
  },
  data: function data() {
    return {
      // 搜索按钮的 loading
      searchLoading: false,
      // form
      form: this.$form.createForm(this),
      formItemLayout: formItemLayout
    };
  },
  methods: {
    /**
     * 搜索活动
     */
    handleSearchActivity: function handleSearchActivity() {
      this.searchLoading = true;
      this.$emit('search', this.trans(this.form.getFieldsValue()));
      this.searchLoading = false;
    },
    trans: function trans(params) {
      console.log(params);

      if (params.location_id !== undefined) {
        params.location_id = params.location_id.location.pop();
      }

      return params;
    },
    handleAddActivity: function handleAddActivity() {
      this.$router.push('/activity/publish');
    }
  }
};